* {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --column-count: 4;
  }
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  flex-shrink: 0;
  align-items: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.nav__list {
  --font-color: #00acdc;
  text-transform: uppercase;
  flex-direction: row;
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__list-item {
  margin-right: 20px;
}

.nav__list-item:last-child {
  margin-right: 0;
}

.nav__list-item .is-active {
  color: var(--font-color);
  position: relative;
}

.nav__list-item .is-active:after {
  content: "";
  background-color: var(--font-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__list-item-link {
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.nav__list-item-link:hover {
  color: #00acdc;
}

.product-card {
  color: #060b35;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 100%;
  max-width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
  position: relative;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card__img {
  justify-content: center;
  display: flex;
}

.product-card__img-size {
  width: 160px;
  height: 134px;
  display: block;
}

.product-card__description {
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card:hover .product-card__description {
  color: #34568b;
}

.product-card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__rating {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.product-card__rating-reviews {
  padding-top: 1px;
  font-size: 10px;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.product-card__price-value {
  color: #616070;
  font-size: 12px;
}

.product-card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: auto;
  flex-shrink: 0;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

